.Trophy {
  width: 40%;
  position: relative;
}
.trophy_img {
  width: 88%;
  height: 80%;
  margin-top: 0.7rem;
}
.prize {
  position: absolute;
  height: 6rem;
  width: 20%;
  right: 12%;
  top: 18%;
  z-index: 100;
}
.frame {
  position: absolute;
  width: 8.5rem;
  height: 8.5rem;
  right: 10%;
  top: 10%;
  border-radius: 50%;
  box-shadow: 0px 0px 25px 8px #32fb00,
    inset 10px 10px 50px 32px rgb(50 251 0 / 68%);
}
.desc {
  position: absolute;
  width: 50%;
  left: 15%;
  top: 20%;
  font-weight: 700;
  font-size: 1.3rem;
  text-align: right;
  color: #ffffff;
}
.lower_desc {
  display: block;
  text-transform: lowercase;
  font-weight: 500;
}
.last {
  position: absolute;
  width: 88%;
  height: 27%;
  bottom: 0;
  font-family: "Arial";
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 31px;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 0 0 2.2rem 2.2rem;
  padding: 1rem 2rem;
  background: rgba(0, 0, 0, 0.5);
  -webkit-border-radius: 0 0 2.2rem 2.2rem;
  -moz-border-radius: 0 0 2.2rem 2.2rem;
  -ms-border-radius: 0 0 2.2rem 2.2rem;
  -o-border-radius: 0 0 2.2rem 2.2rem;
}
.last_time {
  display: block;
  font-size: 1rem;
  font-weight: 500;
}
